import React from 'react';
import AuthStyle from '../../css/authentication/AuthStyle';
import '../../css/authentication/Auth.css';

class Guest extends React.Component{
    constructor(props){
        super(props);
        this.state = {};
        this.addGuest = this.addGuest.bind(this);
        this.removeModal = this.removeModal.bind(this);
        this.clickedForm = this.clickedForm.bind(this);
    }

    componentDidMount(){
        AuthStyle();
        //document.getElementById("guestModal").onclick = this.removeModal;
    }

    componentDidUpdate(){
        AuthStyle();
    }

    render(){
        return(
            <div id="guestModal">
                <form className="form-inline" autoComplete="off" onMouseDown={this.clickedForm} onSubmit={this.addGuest}>
                    <div className="mx-auto row">
                    <input className="input form-control mx-auto col-10"
                        type="text" name="guest" placeholder="Guest name..." autoFocus
                    />
                    <button className="button btn col-2" type="submit"><i className="fas fa-arrow-right"></i></button>
                    </div>
                </form>
            </div>
        );
    }

    clickedForm(){
        this.setState({...this.state, ['clickedForm']: true});
        console.log(this.state.clickedForm);
    }

    removeModal(){
        console.log(this.state.clickedForm);
        if(!this.state.clickedForm){
            //document.getElementById("guestModal").remove();
            window.location.href = "/";
        } else{
            this.setState({...this.state, ['clickedForm']: null});
        }
    }

    addGuest(e){
        e.preventDefault();

        if(e.target["guest"].value != ""){
            let date = "" + Date.now();
            let guest = e.target["guest"].value + "-" + date.substring(date.length - 6);
            
            window.localStorage.setItem("guest", guest);
            console.log(window.localStorage.getItem("guest"));
            window.location.href = "/guest/scan";
        } else {
            window.history.back();
        }
    }
}

export default Guest;